'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property' ;


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name ARGV
* @memberof ns
* @readonly
* @type {Array}
* @see {@link module:@stdlib/process/argv}
*/
import ARGV from './../argv';
setReadOnly( ns, 'ARGV', ARGV );

/**
* @name chdir
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/process/chdir}
*/
import chdir from './../chdir';
setReadOnly( ns, 'chdir', chdir );

/**
* @name cwd
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/process/cwd}
*/
import cwd from './../cwd';
setReadOnly( ns, 'cwd', cwd );

/**
* @name ENV
* @memberof ns
* @readonly
* @type {Object}
* @see {@link module:@stdlib/process/env}
*/
import ENV from './../env';
setReadOnly( ns, 'ENV', ENV );

/**
* @name EXEC_PATH
* @memberof ns
* @readonly
* @type {string}
* @see {@link module:@stdlib/process/exec-path}
*/
import EXEC_PATH from './../exec-path';
setReadOnly( ns, 'EXEC_PATH', EXEC_PATH );

/**
* @name getegid
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/process/getegid}
*/
import getegid from './../getegid';
setReadOnly( ns, 'getegid', getegid );

/**
* @name geteuid
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/process/geteuid}
*/
import geteuid from './../geteuid';
setReadOnly( ns, 'geteuid', geteuid );

/**
* @name getgid
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/process/getgid}
*/
import getgid from './../getgid';
setReadOnly( ns, 'getgid', getgid );

/**
* @name getuid
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/process/getuid}
*/
import getuid from './../getuid';
setReadOnly( ns, 'getuid', getuid );

/**
* @name NODE_VERSION
* @memberof ns
* @readonly
* @type {(string|null)}
* @see {@link module:@stdlib/process/node-version}
*/
import NODE_VERSION from './../node-version';
setReadOnly( ns, 'NODE_VERSION', NODE_VERSION );

/**
* @name stdin
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/process/read-stdin}
*/
import stdin from './../read-stdin';
setReadOnly( ns, 'stdin', stdin );

/**
* @name umask
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/process/umask}
*/
import umask from './../umask';
setReadOnly( ns, 'umask', umask );


// EXPORTS //

export default ns;
